body{
	font-size: 18px;
	margin: 0px 0px;
}

header{
	position: sticky;
	top: 0;
	opacity: 1;
	color: white;
	text-align: center;
	background-color: #060b0ebd;
	width: 100%;
}
h1,h2{
	font-weight: 1;
	width: 100%;
	padding: 10px 0px 10px 0px;
	margin: 0px 0px;
}
.container{
	display: block;
}
#download_button{
	width: 100px;
	color: white;
	border: none;
	font-weight: 5;
	font-size:1.25em;
	border-radius: 30px;
	margin-top: 10px;
	padding: 3px 35px;
	background-color: #103687;
	cursor: pointer;
}
#download_button:hover{
	background-color: white;
	color: black;
}
.feature{
	background-color:#fff3f3;
}
.properties{
	text-align: center;
	margin: 0px -2px;
	width: 25%;
	display: inline-block;
}
img{
	margin-top: 10px;
	mix-blend-mode: multiply;
	width: 50%;
}
.quote{
	text-align: center;
	background-color: #efa4ee;
	padding-top: 10px;
	padding-bottom: 10px;
}
cite:before{
	content: "-";
}
footer{
	color: white;
	text-align: center;
	background-color: #374268;
}
a{
	color: white;
	text-decoration: none;
}
@media screen and (max_width: 480px){
	#download_button{
			background-color: black;
			}
}