*{
  overflow-x: hidden;
  margin: 0px;
  width: 100%;
  
}
body{
  background-color: aliceblue;
}
.navbar{
  
  width: 100%;
  background-color: rgb(173, 126, 126);
  text-transform: uppercase;
}
.navbar ul{
  display: flex;
  flex-wrap: wrap;  
}
.navbar ul li{
  margin: 5px auto;
  list-style-type: none;
}
.navbar ul li a{
  font-weight: bolder;
  font-size: larger;
  text-decoration: none;
  color: rgb(255, 255, 255);
}
.container{
  text-align: center;
}
button{
  width: 90px;
  border: none;
  background-color: blue;
  color: white;
  border-radius: 9px;
}
.todoapp{
  text-align: center;
  width: 100%;
}
#todo_text{
  width: 50%;
}