.sign{
    color:yellow;
}

#container{
    border-radius: 20px;
    color: white;
    background-color: black;
    border: 2px solid white;
    width: 70%;
    margin: auto;
    text-align: center;
}
button{
    color: white;
    background-color: #665353;
     margin-top: 20px;
    width: 24%;
}
input{
    box-shadow: 2px 2px steelblue;
    color: white;
    background-color: black;
    font-size: 30px;
    border-radius: 30px;
    height: 50px;
    width: 100%
}
button:hover{
    cursor: pointer;
    background-color: grey;
}